import { Box, ChakraProvider, Container, Flex, theme } from "@chakra-ui/react";
import * as React from "react";
import './App.scss';
import Footer from "./components/gui/Footer";
import Header from "./components/gui/Header";
import Modules from "./components/gui/Modules";
import { Score } from "./components/gui/Score";
import Main from "./components/Main";
import { generalState } from "./state/state";

export function App() {
  return (
    <ChakraProvider theme={theme}>
      {/* Header */}
      <Header />

      {/* Main */}
      <Flex pl={20} pr={20} pb={10} align={"flex-start"}>
        {/* Left */}
        <Box p={5} borderWidth="2px" borderRadius="lg" w={250}>
          <Modules />
        </Box>

        {/* Center */}
        <Container maxW="container.sm">
          <Main />
        </Container>

        {/* Right */}
        <Box>
          <Score
            right={generalState(_ => _.correctAnswers)}
            wrong={generalState(_ => _.wrongAnswers)}
          />
        </Box>

      </Flex>

      {/* Footer */}
      <Footer />
    </ChakraProvider>
  )
}